'use client';

import React, { useState, useCallback } from 'react';
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Box,
  Avatar,
  Menu,
  MenuItem,
  useTheme,
  useMediaQuery,
  Divider,
  ListItemIcon,
  ListItemText,
  Tooltip,
} from '@mui/material';
import {
  Menu as MenuIcon,
  Logout as LogoutIcon,
  ArrowBackIosNew as ArrowBackIcon,
  Person as PersonIcon,
  Settings as SettingsIcon,
} from '@mui/icons-material';
import { useRouter } from 'next/navigation';
import { useUser } from '@/contexts/UserContext';

/**
 * Professional application toolbar component with responsive design
 * @param {Object} props - Component props
 * @param {Function} props.handleDrawerToggle - Function to toggle navigation drawer
 * @param {string} props.title - Optional custom title for the toolbar
 */
const AppToolbar = ({ 
  handleDrawerToggle, 
  title = 'Dashboard',
  showBackButton = true 
}) => {
  const { user, logout } = useUser();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const router = useRouter();

  const [anchorEl, setAnchorEl] = useState(null);
  const isMenuOpen = Boolean(anchorEl);

  // Memoized event handlers for better performance
  const handleMenuOpen = useCallback((event) => {
    setAnchorEl(event.currentTarget);
  }, []);

  const handleMenuClose = useCallback(() => {
    setAnchorEl(null);
  }, []);

  const handleLogout = useCallback(() => {
    handleMenuClose();
    logout();
  }, [logout, handleMenuClose]);

  const handleProfile = useCallback(() => {
    handleMenuClose();
    router.push('/profile');
  }, [router, handleMenuClose]);

  const handleSettings = useCallback(() => {
    handleMenuClose();
    router.push('/settings');
  }, [router, handleMenuClose]);

  const handleBack = useCallback(() => {
    router.back();
  }, [router]);

  // Get user initials for avatar fallback
  const getUserInitials = (name) => {
    if (!name) return 'U';
    return name
      .split(' ')
      .map(part => part.charAt(0))
      .join('')
      .toUpperCase()
      .slice(0, 2);
  };

  return (
    <AppBar 
      position="fixed" 
      elevation={1}
      sx={{ 
        zIndex: theme.zIndex.drawer + 1,
        backgroundColor: '#0A0F19',
        borderBottom: '1px solid rgba(255, 255, 255, 0.08)',
      }}
    >
      <Toolbar 
        sx={{ 
          display: 'flex', 
          justifyContent: 'space-between',
          minHeight: { xs: 56, sm: 64 },
          px: { xs: 1, sm: 2 },
        }}
      >
        {/* Left section - Navigation and Title */}
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
          {isMobile ? (
            <Tooltip title="Open navigation menu">
              <IconButton 
                edge="start" 
                color="inherit" 
                aria-label="open navigation menu"
                onClick={handleDrawerToggle}
                sx={{ 
                  color: '#00ED64',
                  '&:hover': {
                    backgroundColor: 'rgba(0, 237, 100, 0.08)',
                  },
                }}
              >
                <MenuIcon />
              </IconButton>
            </Tooltip>
          ) : showBackButton && (
            <Tooltip title="Go back">
              <IconButton 
                edge="start" 
                color="inherit" 
                aria-label="go back"
                onClick={handleBack}
                sx={{ 
                  color: '#00ED64',
                  '&:hover': {
                    backgroundColor: 'rgba(0, 237, 100, 0.08)',
                  },
                }}
              >
                <ArrowBackIcon />
              </IconButton>
            </Tooltip>
          )}
          
          <Typography 
            variant={isMobile ? 'h6' : 'h5'}
            component="h1"
            sx={{ 
              color: '#fff', 
              fontWeight: 600,
              fontSize: { xs: '1.1rem', sm: '1.25rem' },
              letterSpacing: '0.02em',
            }}
          >
            {title}
          </Typography>
        </Box>

        {/* Right section - User Menu */}
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          {user && (
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              {!isMobile && (
                <Box sx={{ textAlign: 'right', mr: 1 }}>
                  <Typography 
                    variant="body2" 
                    sx={{ 
                      color: '#fff', 
                      fontWeight: 500,
                      lineHeight: 1.2,
                    }}
                  >
                    {user.name || 'User'}
                  </Typography>
                  {user.email && (
                    <Typography 
                      variant="caption" 
                      sx={{ 
                        color: 'rgba(255, 255, 255, 0.7)',
                        display: 'block',
                        lineHeight: 1,
                      }}
                    >
                      {user.email}
                    </Typography>
                  )}
                </Box>
              )}
              
              <Tooltip title="Account menu">
                <IconButton 
                  onClick={handleMenuOpen}
                  aria-label="account menu"
                  aria-controls={isMenuOpen ? 'account-menu' : undefined}
                  aria-haspopup="true"
                  aria-expanded={isMenuOpen ? 'true' : undefined}
                  sx={{
                    p: 0.5,
                    '&:hover': {
                      backgroundColor: 'rgba(255, 255, 255, 0.08)',
                    },
                  }}
                >
                  <Avatar 
                    alt={user.name || 'User'} 
                    src={user.avatarUrl}
                    sx={{
                      width: 36,
                      height: 36,
                      fontSize: '0.875rem',
                      fontWeight: 600,
                      bgcolor: user.avatarUrl ? 'transparent' : '#00ED64',
                      color: user.avatarUrl ? 'inherit' : '#0A0F19',
                    }}
                  >
                    {!user.avatarUrl && getUserInitials(user.name)}
                  </Avatar>
                </IconButton>
              </Tooltip>
            </Box>
          )}

          <Menu
            id="account-menu"
            anchorEl={anchorEl}
            open={isMenuOpen}
            onClose={handleMenuClose}
            onClick={handleMenuClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            sx={{
              mt: 1.5,
              '& .MuiPaper-root': {
                minWidth: 200,
                borderRadius: 2,
                boxShadow: '0 4px 20px rgba(0, 0, 0, 0.15)',
                border: '1px solid rgba(255, 255, 255, 0.08)',
              },
            }}
          >
            <MenuItem onClick={handleProfile}>
              <ListItemIcon>
                <PersonIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText primary="Profile" />
            </MenuItem>
            
            <MenuItem onClick={handleSettings}>
              <ListItemIcon>
                <SettingsIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText primary="Settings" />
            </MenuItem>
            
            <Divider sx={{ my: 1 }} />
            
            <MenuItem 
              onClick={handleLogout}
              sx={{
                color: theme.palette.error.main,
                '&:hover': {
                  backgroundColor: 'rgba(244, 67, 54, 0.08)',
                },
              }}
            >
              <ListItemIcon>
                <LogoutIcon fontSize="small" sx={{ color: 'inherit' }} />
              </ListItemIcon>
              <ListItemText primary="Logout" />
            </MenuItem>
          </Menu>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default AppToolbar;